


























import { Component, Vue, Watch } from "vue-property-decorator";

@Component({
    components: {}
})
export default class App extends Vue {
  private CustomClassname: any = '';
  get getThemeColor() {
    return this.$store.state.themeColor;
  }

  @Watch('$route', { immediate: true, deep: true })
  onUrlChange(newVal: any) {
    if(this.$route.name === 'GameIframe') {
      this.CustomClassname = this.$route.name;
    } else {
      this.CustomClassname = '';
    }
  }

  private changeLang(lang: any) {
    localStorage.setItem('currLang', lang)
    window.location.reload()
  }

  private changeTheme(color: any) {
    this.$store.dispatch('setThemeSkin',  color)
    window.location.reload()
  }
}
